<template>
  <div>
    <div id="webgl" ref="webgl"></div>
  </div>
</template>

<script>
// 引入three.js
import * as THREE from "three";
// 引入dat.gui.js的一个类GUI
import { GUI } from "three/addons/libs/lil-gui.module.min.js";
// 引入轨道控制器扩展库OrbitControls.js
import { OrbitControls } from "three/addons/controls/OrbitControls.js";

export default {
  mounted() {
    // 三维场景
    const scene = new THREE.Scene();
    scene.background = new THREE.Color("#FFC0CB");
    // 模型对象
    const geometry = new THREE.BoxGeometry(100, 100, 100);
    const texLoader = new THREE.TextureLoader();

    // .load()方法加载图像，返回一个纹理对象Texture
    const texture = texLoader.load(require("../assets/456.jpg"));
    // 添加一个辅助网格地面
    const gridHelper = new THREE.GridHelper(300, 25, 0x004444, 0x004444);

    const material = new THREE.MeshBasicMaterial({
      map: texture,
      side: THREE.DoubleSide,
    });
    scene.add(gridHelper);
    const mesh = new THREE.Mesh(geometry, material);
    scene.add(mesh);
    // AxesHelper：辅助观察的坐标系
    const gui = new GUI();
    gui.close();
    gui.add(mesh.rotation, "x", 0, 180);
    gui.add(mesh.rotation, "y", 0, 180);
    gui.add(mesh.rotation, "z", 0, 180);

    const axes = new THREE.AxesHelper(50);
    mesh.add(axes);

    const width = window.innerWidth; //宽度
    const height = window.innerHeight; //高度
    // 相机
    const camera = new THREE.PerspectiveCamera(30, width / height, 1, 3000);
    camera.position.set(292, 223, 185);
    camera.lookAt(0, 0, 0);
    // WebGL渲染器
    const renderer = new THREE.WebGLRenderer({
      antialias: true,
    });
    renderer.setSize(width, height);

    //three.js执行渲染命令会输出一个canvas画布(HTML元素)，你可以插入到web页面中
    this.$refs.webgl.appendChild(renderer.domElement);
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true
// const clock = new THREE.Clock()
/* const cursor = {
  x:0,
  y:0
}
document.addEventListener("mousemove",(e)=>{
  cursor.x = e.clientX/width - 0.5
  cursor.y = e.clientY/height - 0.5
}) */
    function render() {
      // mesh.rotation.x += Math.sin(clock.getElapsedTime())*0.01
      // mesh.rotation.y += Math.cos(clock.getElapsedTime())*0.01
/*       camera.position.x = Math.sin(cursor.x * Math.PI *2) *400
      camera.position.z = Math.cos(cursor.x * Math.PI *2) *400
      camera.position.y = cursor.y *500 */
      camera.lookAt(mesh.position)
      renderer.render(scene, camera); //执行渲染操作

      controls.update();
      requestAnimationFrame(render); //请求再次执行渲染函数render，渲染下一帧
    }
    render();
  },
};
</script>

<style scoped></style>
